import { removeItem, setItem, getItem } from './storage';

const QUEUE_STORAGE_KEY = 'chengyi_offline_queue';
const MAX_QUEUE_SIZE = 100;
const MAX_RETRY_COUNT = 3;

export interface QueuedRequest {
  id: string;
  url: string;
  method: string;
  body?: any;
  headers?: Record<string, string>;
  timestamp: number;
  retryCount: number;
}

export class OfflineQueueService {
  private queue: QueuedRequest[] = [];
  private isProcessing = false;
  private onlineCallback?: () => void;

  constructor() {
    void this.loadQueue();
    this.setupOnlineListener();
  }

  /**
   * 
   */
  private async loadQueue(): Promise<void> {
    try {
      const stored = await getItem(QUEUE_STORAGE_KEY);
      if (stored) {
        this.queue = JSON.parse(stored);
      }
    } catch (error) {
      console.error(':', error);
      this.queue = [];
    }
  }

  /**
   *  localStorage
   */
  private async saveQueue(): Promise<void> {
    try {
      if (this.queue.length === 0) {
        await removeItem(QUEUE_STORAGE_KEY);
      } else {
        await setItem(QUEUE_STORAGE_KEY, JSON.stringify(this.queue));
      }
    } catch (error) {
      console.error(':', error);
    }
  }

  /**
   * 
   */
  private setupOnlineListener(): void {
    if (typeof window === 'undefined' || typeof window.addEventListener !== 'function') {
      return;
    }

    window.addEventListener('online', () => {
      console.log('');
      void this.processQueue();
      if (this.onlineCallback) {
        this.onlineCallback();
      }
    });
  }

  /**
   * 
   */
  public setOnlineCallback(callback: () => void): void {
    this.onlineCallback = callback;
  }

  /**
   * 
   */
  public enqueue(request: Omit<QueuedRequest, 'id' | 'timestamp' | 'retryCount'>): void {
    if (this.queue.length >= MAX_QUEUE_SIZE) {
      console.warn('');
      this.queue.shift();
    }

    const queuedRequest: QueuedRequest = {
      ...request,
      id: `${Date.now()}-${Math.random().toString(36).substring(2, 9)}`,
      timestamp: Date.now(),
      retryCount: 0
    };

    this.queue.push(queuedRequest);
    void this.saveQueue();

    console.log(`: ${request.method} ${request.url}`);
  }

  /**
   * 
   */
  public async processQueue(
    fetcher?: (url: string, opts: RequestInit) => Promise<string>
  ): Promise<void> {
    if (this.isProcessing || this.queue.length === 0) return;
    if (typeof navigator !== 'undefined' && !navigator.onLine) return;

    this.isProcessing = true;
    console.log(` ${this.queue.length} `);

    const failedRequests: QueuedRequest[] = [];

    while (this.queue.length > 0) {
      const request = this.queue.shift()!;

      try {
        if (fetcher) {
          await fetcher(request.url, {
            method: request.method,
            headers: request.headers,
            body: request.body ? JSON.stringify(request.body) : undefined
          });
        } else {
          const response = await fetch(request.url, {
            method: request.method,
            headers: request.headers,
            body: request.body ? JSON.stringify(request.body) : undefined
          });

          if (!response.ok) {
            throw new Error(`HTTP ${response.status}`);
          }
        }

        console.log(`: ${request.method} ${request.url}`);
      } catch (error) {
        console.error(`: ${request.method} ${request.url}`, error);

        if (request.retryCount < MAX_RETRY_COUNT) {
          failedRequests.push({
            ...request,
            retryCount: request.retryCount + 1
          });
        } else {
          console.warn(`: ${request.method} ${request.url}`);
        }
      }

      // 
      await new Promise(resolve => setTimeout(resolve, 500));
    }

    // 
    this.queue = failedRequests;
    await this.saveQueue();

    this.isProcessing = false;
    console.log(` ${this.queue.length} `);
  }

  /**
   * 
   */
  public getQueueLength(): number {
    return this.queue.length;
  }

  /**
   * 
   */
  public clearQueue(): void {
    this.queue = [];
    void this.saveQueue();
    console.log('');
  }

  /**
   * 
   */
  public hasPendingRequests(): boolean {
    return this.queue.length > 0;
  }
}

// 
let instance: OfflineQueueService | null = null;

export function getOfflineQueueService(): OfflineQueueService {
  if (!instance) {
    instance = new OfflineQueueService();
  }
  return instance;
}
